@import url(https://fonts.googleapis.com/css2?family=New+Tegomin&display=swap);
@import url(https://fonts.googleapis.com/css2?family=New+Tegomin&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  font-family: 'Open Sans Condensed';
  padding: 20px 40px;
}

a {
  text-decoration: none;
  color: black;
}

* {
  box-sizing: border-box;
}

.cart-icon{width:45px;height:45px;position:relative;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;cursor:pointer}.cart-icon .shopping-icon{width:24px;height:24px}.cart-icon .item-count{position:absolute;font-size:10px;font-weight:bold;bottom:12px}

.custom-button{min-width:165px;width:auto;height:50px;letter-spacing:0.5px;line-height:50px;padding:0 35px 0 35px;font-size:15px;background-color:black;color:white;text-transform:uppercase;font-family:'Open Sans Condensed';font-weight:bolder;border:none;cursor:pointer;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center}.custom-button:hover{background-color:white;color:black;border:1px solid black}.custom-button.google-sign-in{background-color:#4285f4;color:white}.custom-button.google-sign-in:hover{background-color:#357ae8;border:none}.custom-button.inverted{background-color:white;color:black;border:1px solid black}.custom-button.inverted:hover{background-color:black;color:white;border:none}

.cart-item{width:100%;display:-webkit-flex;display:flex;height:80px;margin-bottom:15px}.cart-item img{width:30%}.cart-item .item-details{width:70%;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:flex-start;align-items:flex-start;-webkit-justify-content:center;justify-content:center;padding:10px 20px}.cart-item .item-details .name{font-size:16px}

.cart-dropdown{position:absolute;width:240px;height:340px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;padding:20px;border:1px solid black;background-color:white;top:90px;right:40px;z-index:5}.cart-dropdown .empty-message{font-size:18px;margin:50px auto}.cart-dropdown .cart-items{height:240px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;overflow:scroll}.cart-dropdown button{margin-top:auto}

.header{height:70px;width:100%;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;margin-bottom:25px}.header .logo-container{height:100%;width:70px}.header .options{width:50%;height:100%;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:flex-end;justify-content:flex-end}.header .options .option{padding:10px 15px;cursor:pointer}

.collection-item{width:22%;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;height:350px;-webkit-align-items:center;align-items:center;position:relative}.collection-item .image{width:100%;height:95%;background-size:cover;background-position:center;margin-bottom:5px}.collection-item .custom-button{width:80%;opacity:0.7;position:absolute;top:255px;display:none}.collection-item:hover .image{opacity:0.8}.collection-item:hover .custom-button{opacity:0.85;display:-webkit-flex;display:flex}.collection-item .collection-footer{width:100%;height:5%;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;font-size:18px}.collection-item .collection-footer .name{width:90%;margin-bottom:15px}.collection-item .collection-footer .price{width:20%}

.collection-preview{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;margin:0 auto;width:90%}.collection-preview .title{font-size:20px;margin-top:35px;margin-bottom:10px}.collection-preview .preview{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;font-family:'New Tegomin', serif}

.bm-container{height:auto;width:100%;display:-webkit-flex;display:flex;-webkit-justify-content:flex-start;justify-content:flex-start;margin-top:70px;margin-bottom:0px}.bm-container .bm-items{margin:0px 15px;cursor:pointer}.bm-container .h-line{margin-top:0;width:100%}

.all-products{font-family:'New Tegomin', serif}.information{margin-top:200px}

.information-section{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}.information-section .info-columns{width:20%;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.information-section .info-columns .coloumn-title{font-size:20;margin-bottom:20px}.information-section .info-columns .column-item{font-size:12;padding:5px;margin-bottom:5px;cursor:pointer}.information-section .info-columns .hover-effect:hover{margin-left:10px}

